$border-radius: 10px;

.thumb {
    display: flex;
    align-items: flex-end;
    position: relative;
    width: 100%;
    height: 340px;
    color: #fff;
    border-radius: $border-radius;
    z-index: 10;
}

.thumb__title {
    margin: 0 20px 20px 20px;
    width: 100%;
    font-size: 1em;
    z-index: 1000;
}

.thumb.filter::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: inherit;
}

.thumb__img {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    z-index: -1;
}

@media screen and (max-width:375px) {
    .thumb {
        height: 255px;
    }

    .thumb__title {
        margin: 0px 15px 15px 15px;
        font-size: 1.5em;
    }
}